@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rootContainer{
    background-color : #CED2DC;
    height : 100vh;
}
.root{
    width  : 42%;
    height : 100vh;
}
.height-100{
    height : 100%
}
.play-button-container{
    height:100%;
    padding-left:11.18% ;
}
.play-button-text{
    letter-spacing: 4.4px;
    color: #424242;
    margin:0px 0px 0px 6px 
}
.play-button-icon{
    color : #fff;
    font-size: 2rem !important;
}
.wrapper-play-button{
    width:19.5%;
    display: inline-block;
    position: relative;
}
.wrapper-play-button:after {
    padding-top: 100%;
    display: block;
    content: '';
}
.play-button{
    padding-top:26%;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: #FF7243 0% 0% no-repeat padding-box;
    box-shadow: 10px 10px 50px #FF724373;
    border-radius: 20px;
    opacity: 1;
}
.header{
    width  : 42%;
    margin : 0 auto;
    height : 24vh;
    padding-left: 2.625%;
    padding-bottom: 1.7325%;
}
.title{
    color: #3D4276;
    font-size : 1rem !important;
    font-weight: 600 !important;
    letter-spacing: 0 !important;
}
.subtitle{
    color: #949494;
    font-weight: 400 !important;
    width: 25%;
    letter-spacing: 0 !important;
}
.divider{
    width  : 85%;
    margin : 0 auto !important;
}
.main{
    height : 62vh;
    background-color: #fff;
    box-shadow: 0px 3px 10px #00000029;
    border: 1px solid #F7F7F7;
    border-radius: 30px;
    opacity: 1;
    position: relative;
    z-index : 100
}
.main-item{
    flex-grow: 1;
    padding  : 5.5%
}
.mainImage{
    width:59%;
    height:77%;
    position: absolute;
    margin: -50% 0px 0px 45%;
}
.game-option-container{
    width:47.5%;
    border-radius: 20px;
    opacity: 1;
    padding: 6px;
    cursor: pointer;
    margin-bottom: 6px;
}
.game-option-title{
    letter-spacing: 0px;
    color: #FFFFFF;
    opacity: 1;
    font-weight: 500 ;
    border: 0; background: none;
    cursor: pointer;
}
.game-option-title:focus{border: none; outline: none;}
.game-option-image-wrapper{
    width:17%;
    margin:0px 20px;
}
.game-option-image{
    height:0;
    padding-top: 100%;
    width:100%;
}
.footer{
    flex-grow: 1;
}
.footerPanel{
    background-color: #eeeeee;
    border-radius: 30px;
    opacity: 1;
    height:50vh;
    margin-top: -40vh;
}
.trademark{
    padding-bottom:4% ;
    padding-left : 7%;
}
.footText{
    font-size:0.725rem !important
}
.board-wrapper{
    width:65%;
    display: inline-block;
    position: relative;
    border-radius: 30px;
    background-color: #fff;
}
.board-wrapper:after {
    padding-top: 100%;
    display: block;
    content: '';
}
.board{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    border-radius: 30px;
    padding:3%;
    background: #FFF;
    border: 2px solid #FFF;
    box-shadow: 2px 2px 4px 0px rgba(0,0,0,0.1);
}
.height-100{
    height :100%
}
.board-cell{
    background-color: #fff;
    border-radius: 20px;
    width: 12%;
    border-radius: 100%;
    display: flex;
    align-items: center;
}

.board-elements{
    background-color: #84a4fc;
    border-radius: 18px;
}

.board-cell .MuiCardMedia-root{display: inline-block; margin: 0 auto; border-radius: 100%;}

.statictics-container{padding: 3%;}
.statistics-panel{
    width:35%;
    padding: 1%;
}
.panel{
    display : flex;
    align-items: center;
    justify-content: space-around;
}

.board_elements{position: relative; z-index: 1;}
/* .board_elements::after{content: ""; position: absolute; background: #84a4fc; width: 92%; height: 92%; z-index: 0;} */

.statistics-panel h1, .statistics-panel h2{font-weight: 500; margin: 6px 0;}
.statistics-panel h1{color: #FF6600; font-weight:600; font-size: 36px;}
.statistics-panel h2{color:#424242;  font-size: 30px;}
.statistics-panel h3{margin: 0; font-weight: 500;}

.board-cell p{  margin: 0 auto; font-size: 24px;    color: #FFF; display: block;}

.twoplayersgame_pl1{background: #dcf6e4; border: 1px solid #d7efde; margin: 10px 0; padding: 3%;}
.twoplayersgame_pl1 div:nth-child(2), .twoplayersgame_pl2 div:nth-child(2){width: 35%;}
.twoplayersgame_pl1 div:nth-child(3), .twoplayersgame_pl2 div:nth-child(3){width: 20%;}

.twoplayersgame_pl2{background: #f6efd5; border: 1px solid #e7e1ca;padding: 3%;}

.MuiDivider-root{margin: 10px 0;}
.active{background: #FFA200;}
.panel-image{ width: 63px; height: 63px; margin-right: 12px; text-align: center; display: flex; align-items: center; border-radius: 100px;}
.endgame__button{width: 100%;}
.endgame__button .MuiButton-label{background: #FFF; border-radius: 16px; color: #cc0000; cursor: pointer; padding: 6% 3%; font-size: 20px;
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.15);}
.player1-board-cell-container{
    width:100%;
    border-radius: 100%;
    border:4px solid #37ac5d
}
.player2-board-cell-container{
    width:100%;
    border-radius: 100%;
    border:4px solid #F8D146
}
.root-container{
    background-color : #CED2DC;
    height : 100vh;
}
.app-bar{
    height : 7.5%;
    background-color: #fff;
}
.header-title{
    font-weight: 500;
    margin:0 auto;
    letter-spacing: 0px;
    color: #424242;
    opacity: 1;
}
.main-root{
    width : 26.33%;
    height: 70.75vh;
    padding:3.33%;
    margin:0 auto !important;
    background-color: #fff;
    box-shadow: 0px 3px 6px #00000029;
    border: 1px solid #F7F7F7;
    border-radius: 30px;
    opacity: 1;
}
.panel{
    width:94%;
}

.players_wrap{padding: 40px;}
.players_wrap .panel{border-radius: 10px; display: flex; align-items: center; padding: 3%; margin-bottom: 8px;}

.players_wrap .panel:nth-child(1){background: #dcf6e4; border: 1px solid #d7efde;}
.players_wrap .panel:nth-child(2){background: #f6efd5; border: 1px solid #e7e1ca;}
.players_wrap .panel:nth-child(3){background: #eff3ff; border: 1px solid #e5e9f4;}
.players_wrap .panel:nth-child(4){background: #eff3ff; border: 1px solid #e5e9f4;}

.players_wrap .panel .panel-image{ width: 63px; height: 63px; margin-right: 12px; text-align: center; display: flex; align-items: center; border-radius: 100px;}
.players_wrap .panel:nth-child(1) .panel-image{border: 8px solid #37ac5d;}
.players_wrap .panel:nth-child(2) .panel-image{border: 8px solid #f8d146;}
.players_wrap .panel:nth-child(1) .panel-image,
.players_wrap .panel:nth-child(2) .panel-image
{
box-shadow: 3px 3px 8px 0px rgba(0,0,0,0.3);
}
::-webkit-input-placeholder {
    opacity: 0.3; /* Firefox */
}
:-ms-input-placeholder {
    opacity: 0.3; /* Firefox */
}
::-ms-input-placeholder {
    opacity: 0.3; /* Firefox */
}
::placeholder {
    opacity: 0.3; /* Firefox */
}

.error-message{
    color: red;
    font-size: 12px;
    height: 15px;
}

.players_wrap .panel:nth-child(3) .panel-image{border: 8px solid #b1c4f9;}
.players_wrap .panel:nth-child(4) .panel-image{border: 8px solid #b1c4f9;}

.players_wrap .MuiCardMedia-root{width:45px; height:55px; border-radius:100%; margin: 0 auto; }

.players_wrap .panel .panel-input{text-align: left; width: 80%;}
.players_wrap .panel .panel-input input, .players_wrap .panel .panel-input p:nth-child(2){ border: 0; border-bottom: 1px solid #c5dacc;
    background: no-repeat; font-size: 24px; color: #424242; width: 100%;}

.startgame__button{width: 100%;}
.startgame__button .MuiButton-label{background: #4b7bff; border-radius: 16px; color: #FFF; cursor: pointer; padding: 6% 3%; font-size: 20px;;
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.15);}

.main-root .MuiDivider-root{margin: 4% 0;}

.d-inblock{display: inline-block;}
.text-left{text-align: left;}
p{margin: 5px 0;}
textarea:focus, input:focus{
    outline: none;
}
.file-input{
    display: none;
}
.player-image{
    width  : 50px;
    height : 50px;
    margin : 0 0 0 5px;
}
.player-image-container{
    width:100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

